.range-slider{
  position: relative;
  width: 300px;
  /* height: 300px; */
  float: left;
  text-align: center;
}
    
.range-slider input[type="range"] {
    pointer-events: none;
    position: absolute;
    -webkit-appearance: none;
    /* -webkit-tap-highlight-color: rgba(255, 255, 255, 0);     */
    border: none;
    border-radius: 14px;
    background: #fffdfd8f;
    /* opacity: 0.7; */
    box-shadow: inset 0 1px 0 0 darken(#fffdfd,15%), inset 0 -1px 0 0 darken(#fffdfd,10%);
    -webkit-box-shadow: inset 0 1px 0 0 darken(#fffdfd,15%), inset 0 -1px 0 0 darken(#fffdfd,10%);
    /* overflow: hidden; */
    left: 0;
    top: 50px;
    width: 300px;
    outline: none;
    height: 8px;
    margin: 0;
    padding: 0;
    width: 80%;
    /* height: 8px;
    width: 300px;
    border-radius: 5px;
    background: #d3d3d3;
    outline: none;
    -webkit-transition: .2s;
    transition: opacity .2s; */
  }  
  
  .range-slider input[type="range"]::-webkit-slider-thumb {
    pointer-events: all;
    position: relative;
    z-index: 1;
    outline: 0;    
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 14px;
    cursor: pointer;
    background: linear-gradient(#413F41,#c9bdbd);
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0%, lighten(#413F41,60%)), color-stop(100%, #413F41)); /* android <= 2.2 */
    background-image: -webkit-linear-gradient(top , lighten(#413F41,60%) 0, #413F41 100%); /* older mobile safari and android > 2.2 */;
    background-image: linear-gradient(to bottom, lighten(#413F41,60%) 0, #413F41 100%); /* W3C */
  }
  
  .range-slider input[type="range"]::-moz-range-thumb {
    pointer-events: all;
    position: relative;
    z-index: 10;
    -moz-appearance: none;
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 14px;
    background-image: linear-gradient(to bottom, lighten(#413F41,60%) 0, #413F41 100%); /* W3C */
  }

  .range-slider input[type="range"]::-ms-thumb {
    pointer-events: all;
    position: relative;
    z-index: 10;
    -ms-appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 14px;
    border: 0;
    background-image: linear-gradient(to bottom, lighten(#413F41,60%) 0, #413F41 100%); /* W3C */
  }
  
  .range-slider input[type=range]::-moz-range-track {
    position: relative;
    z-index: -1;
    background-color: rgba(0, 0, 0, 1);
    border: 0;
  }
  
  .range-slider input[type=range]:last-of-type::-moz-range-track {
    -moz-appearance: none;
    background: none transparent;
    border: 0;
  } 
  
  .range-slider input[type=range]::-moz-focus-outer {
    border: 0;
  }


